<template>
  <div class="mask">
    <slot name="background">
      <div @click="closeMaskClick" class="mask__background"></div>
    </slot>
    <div class="mask__prompt-panel">
      <slot name="top">
        <div class="mask__prompt-panel__title">{{ defaultData.title }}</div>
      </slot>
      <slot name="middle">
        <div class="mask__prompt-panel__message">
          {{ defaultData.message }}
        </div>
      </slot>
      <slot name="bottom">
        <div class="mask__prompt-panel__button">
          <div
            @click="closeMaskClick"
            class="mask__prompt-panel__button__cancel"
          >
            {{ defaultData.cancel }}
          </div>
          <div
            @click="confirmSubmit"
            class="mask__prompt-panel__button__confirm"
          >
            {{ defaultData.confirm }}
          </div>
        </div>
      </slot>
    </div>
  </div>
</template>
<script>
export default {
  name: "Mask",
  props: ["defaultData"],

  setup(props, context) {
    const closeMaskClick = () => {
      context.emit("closeMaskClick");
    };
    
    const confirmSubmit = () => {
      context.emit("confirmSubmit");
    };

    return { closeMaskClick, confirmSubmit };
  },
};
</script>
<style lang="scss" scoped>
.mask {
  &__background {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba($color: #000000, $alpha: 0.5);
  }

  &__prompt-panel {
    width: 301rem;
    height: 157rem;
    background: #ffffff;
    border-radius: 4rem;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 24rem;
    box-sizing: border-box;
    text-align: center;

    &__title {
      font-family: PingFangSC-Medium;
      font-size: 18rem4rem;
      color: #333333;
      font-weight: 700;
    }
    &__message {
      font-family: PingFangSC-Regular;
      font-size: 14rem;
      color: #666666;
      margin-top: 12rem;
    }

    &__button {
      width: 100%;
      position: absolute;
      bottom: 24rem;
      left: 0;

      &__cancel,
      &__confirm {
        width: 80rem;
        height: 32rem;
        border: 1rem4rem solid #4fb0f9;
        border-radius: 16rem4rem;
        font-family: PingFangSC-Regular;
        font-size: 14rem;
        line-height: 32rem;
        display: inline-block;
        margin: 0 12rem;
      }
      &__cancel {
        color: #0091ff;
      }

      &__confirm {
        color: #fff;
        background: #4fb0f9;
      }
    }
  }
}
</style>
