<template>
  <div class="container">
    <div class="container__top">
      <div class="container__top__header">
        <div
          @click="router.back()"
          class="container__top__header__back iconfont"
        >
          &#xe697;
        </div>
        <div class="container__top__header__title">确认订单</div>
      </div>
      <div class="container__top__receiver">
        <div class="container__top__receiver__left">
          <div class="container__top__receiver__left__title">收货地址</div>
          <div class="container__top__receiver__left__address">
            北京理工大学国防科技园2号楼10层
          </div>
          <div class="container__top__receiver__left__info">
            <span class="container__top__receiver__left__info__name">
              瑶妹（先生）
            </span>
            <span class="container__top__receiver__left__info__phone">
              18911024266
            </span>
          </div>
        </div>
        <div class="container__top__receiver__right iconfont">&#xe6a3;</div>
      </div>
    </div>

    <div class="container__order">
      <div class="container__order__shop-name">{{ shopName }}</div>
      <div class="container__order__info">
        <div
          v-for="item in Object.keys(cartList).slice(0, 2)"
          :key="cartList[item].id"
          class="container__order__info__item"
        >
          <img
            :src="cartList[item].imgUrl"
            alt=""
            class="container__order__info__item__img"
          />
          <div class="container__order__info__item__text">
            <div class="container__order__info__item__text__title">
              {{ cartList[item].name }}
            </div>
            <div class="container__order__info__item__text__price">
              <span class="container__order__info__item__text__price__left">
                {{ cartList[item].promotionPrice }} ✖ {{ cartList[item].count }}
              </span>
              <span class="container__order__info__item__text__price__right">
                {{
                  (
                    cartList[item].promotionPrice * cartList[item].count
                  ).toFixed(2)
                }}
              </span>
            </div>
          </div>
        </div>

        <template v-if="!showMore">
          <div
            v-for="item in Object.keys(cartList).slice(2)"
            :key="cartList[item].id"
            class="container__order__info__item"
          >
            <img
              :src="cartList[item].imgUrl"
              alt=""
              class="container__order__info__item__img"
            />
            <div class="container__order__info__item__text">
              <div class="container__order__info__item__text__title">
                {{ cartList[item].name }}
              </div>
              <div class="container__order__info__item__text__price">
                <span class="container__order__info__item__text__price__left">
                  {{ cartList[item].promotionPrice }} ✖
                  {{ cartList[item].count }}
                </span>
                <span class="container__order__info__item__text__price__right">
                  {{
                    (
                      cartList[item].promotionPrice * cartList[item].count
                    ).toFixed(2)
                  }}
                </span>
              </div>
            </div>
          </div>
        </template>
        <div
          @click="showMore = !showMore"
          v-if="showMore && Object.keys(cartList).length > 2"
          class="container__order__info__more iconfont"
        >
          共计{{ Object.keys(cartList).length }}种/{{ sumNumber }}件 &#xe63d;
        </div>
      </div>
    </div>

    <div class="container__bottom">
      <div class="container__bottom__left">
        实付金额<span class="container__bottom__left__total-price">
          ￥{{ sumPrice }}
        </span>
      </div>
      <div
        @click="defaultData.showMask = true"
        class="container__bottom__right"
      >
        提交订单
      </div>
    </div>

    <Mask
      @closeMaskClick="defaultData.showMask = false"
      @confirmSubmit="confirmSubmit"
      v-if="defaultData.showMask"
      :defaultData="defaultData"
    >
    </Mask>

    <Mask v-if="showMask">
      <template #top>
        <div @click="toOrderList" class="container__mask__top iconfont">
          &#xe747;
        </div>
      </template>
      <template #middle>
        <div class="container__mask__middle iconfont">&#xe61b;</div>
      </template>
      <template #bottom>
        <div class="container__mask__bottom">支付成功, 等待配送</div>
      </template>
    </Mask>
  </div>
</template>
<script>
import { reactive, ref } from "@vue/reactivity";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "vuex";
import Mask from "@/components/Mask.vue";

const useOrderEffect = () => {
  const route = useRoute();
  const store = useStore();

  const cartData = store.state.cartData;
  const shopId = route.params.shopId;
  const shopName = cartData[shopId].shopName;
  const cartList = cartData[shopId].itemList;

  let sumPrice = 0;
  let sumNumber = 0;
  for (const key in cartList) {
    sumPrice += cartList[key].count * cartList[key].promotionPrice;
    sumNumber += cartList[key].count;
  }

  sumPrice = sumPrice.toFixed(2);

  let showMore = ref(true);

  return {
    shopName,
    cartList,
    sumPrice,
    showMore,
    sumNumber,
  };
};

const useShowMaskEffect = () => {
  const store = useStore();
  const route = useRoute();
  const router = useRouter();
  const shopId = route.params.shopId;

  const defaultData = reactive({
    title: "确认要离开收银台?",
    message: "请尽快完成支付,否则将被取消",
    cancel: "取消订单",
    confirm: "确认支付",
    showMask: false,
  });

  const showMask = ref(false);

  const confirmSubmit = () => {
    defaultData.showMask = false;
    showMask.value = true;

    store.commit("clearCart", { shopId });
  };

  const toOrderList = () => {
    showMask.value = false;
    router.push({ name: "orderList" });
  };

  return { defaultData, showMask, confirmSubmit, toOrderList };
};

export default {
  components: { Mask },
  name: "orderConfirmation",

  setup() {
    const router = useRouter();
    const { shopName, cartList, sumPrice, showMore, sumNumber } =
      useOrderEffect();

    const { defaultData, showMask, confirmSubmit, toOrderList } =
      useShowMaskEffect();

    return {
      router,
      shopName,
      cartList,
      sumPrice,
      showMore,
      sumNumber,
      defaultData,
      showMask,
      confirmSubmit,
      toOrderList,
    };
  },
};
</script>
<style lang="scss" scoped>
.container {
  position: fixed;
  top: 0;
  bottom: 49rem;
  left: 0;
  right: 0;
  background: #f8f8f8;

  &__top {
    &__header {
      height: 142.5rem;
      text-align: center;
      padding-top: 24.5rem;
      box-sizing: border-box;
      background-image: linear-gradient(
        0deg,
        rgba(0, 145, 255, 0) 4%,
        #0091ff 50%
      );

      &__back {
        font-size: 28rem;
        color: #ffffff;
        position: absolute;
        top: 18rem;
        left: 10.5rem;
      }

      &__title {
        display: inline-block;
        font-family: PingFangSC-Regular;
        font-size: 16rem;
        color: #ffffff;
        text-align: center;
      }
    }

    &__receiver {
      width: 339rem;
      height: 111rem;
      background: #ffffff;
      border-radius: 4rem;
      position: absolute;
      top: 68.5rem;
      left: 18rem;
      right: 18rem;
      padding: 16rem;
      box-sizing: border-box;
      font-family: PingFangSC-Medium;
      color: #333333;
      display: flex;

      &__left {
        flex: 1;

        &__title {
          font-size: 16rem4rem;
          font-weight: 700;
        }

        &__address {
          font-size: 14rem;
          margin-top: 14rem;
          margin-bottom: 6rem;
        }

        &__info {
          font-size: 12rem4rem;
          color: #666666;
        }
      }

      &__right {
        color: #666666;
        font-size: 20rem;
        line-height: 84rem;
      }
    }
  }

  &__order {
    overflow-y: auto;
    width: 339rem;
    max-height: 400rem;
    background: #ffffff;
    border-radius: 4rem;
    padding: 16rem;
    box-sizing: border-box;
    position: relative;
    top: 53rem;
    left: 18rem;
    right: 18rem;
    font-family: PingFangSC-Medium;
    font-size: 14rem;
    color: #333333;

    &__shop-name {
      font-size: 16rem;
      font-weight: 700;
      margin-bottom: 16rem;
    }

    &__info {
      &__item {
        display: flex;
        margin-bottom: 16rem;

        &__img {
          width: 46rem;
          right: 46rem;
          margin-right: 16rem;
        }

        &__text {
          flex: 1;
          position: relative;

          &__title {
            font-weight: 700;
          }

          &__price {
            position: absolute;
            width: 100%;
            bottom: 0;
            left: 10rem;

            &__left {
              color: #e93b3b;
            }

            &__left::before {
              content: "￥";
              font-size: 10rem;
              color: #e93b3b;
              position: absolute;
              left: -9rem;
              bottom: 0rem;
            }

            &__right {
              position: absolute;
              right: 0;
              color: #000000;
            }

            &__right::before {
              content: "￥";
              font-size: 10rem;
              color: #000000;
              position: absolute;
              left: -9rem;
              bottom: 0rem;
            }
          }
        }
      }

      &__more {
        width: 100%;
        height: 28rem;
        background: #f5f5f5;
        text-align: center;
        font-family: PingFangSC-Light;
        font-size: 14rem;
        color: #999999;
        line-height: 28rem;
      }
    }
  }

  &__bottom {
    width: 100%;
    height: 49rem;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    font-family: PingFangSC-Medium;
    font-size: 14rem;
    line-height: 49rem;
    background: #fff;

    &__left {
      flex: 1;
      color: #333333;
      margin-left: 24rem;

      &__total-price {
        font-size: 16rem;
        font-weight: 700;
        margin-left: 5rem;
      }
    }

    &__right {
      width: 98rem;
      height: 100%;
      background: #4fb0f9;
      color: #ffffff;
      text-align: center;
    }
  }

  &__mask {
    &__top {
      position: absolute;
      top: 12rem;
      right: 12rem;
      font-size: 20rem;
      color: #666;
    }

    &__middle {
      font-size: 45rem;
      font-weight: 700;
      color: #000;
      margin-top: 18rem;
    }

    &__bottom {
      font-family: PingFangSC-Medium;
      font-weight: 700;
      font-size: 18rem4rem;
      color: #333333;
      margin-top: 8rem;
    }
  }
}
</style>
